import React from "react";
import styled from "styled-components";
import SpriteHub from "../../lib/sprite_list.json";
import mergeImages from "merge-images";
import { useEffect,useState } from "react";

const Background = styled.div`
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
`;
const CharImage = styled.img`
  width: auto;
  height: 80%;
`;

function CharacterImage(props) {
  const [src,setSrc] = useState("")

  useEffect(()=>{
    var num = "1"
    var name = ""
    if (props.char.split("#").length > 1) {
      [name,num] = props.char.split("#");
    } else {
      name = props.char.split("#")[0];
    }
    var char = SpriteHub[name]
    if (
      char["FacePos"]["x"] > 0 &&
      char["FacePos"]["y"] > 0
    ) {
      var base = Object.keys(char.sprites)[Object.keys(char.sprites).length - 1];
      mergeImages([
        {
          src: `./images/characters/${name}/${char["sprites"][base]["sprite"]}`,
          x: 0,
          y: 0,
        },
        {
          src: `./images/characters/${name}/${char["sprites"][num]["sprite"]}`,
          x: char.FacePos.x,
          y: char.FacePos.y,
        },
      ]).then(result=>{
        setSrc(result)
      })
    } else {
      var sprite = SpriteHub[name]["sprites"][num].sprite;
      setSrc (`./images/characters/${name}/${sprite}`);
    }
  },[props.char,src])

  return (
    <Background onClick={() => props.close()}>
      <CharImage src={`${src}`}></CharImage>
    </Background>
  );
}

export default CharacterImage;
