import { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import mergeImages from "merge-images";

const skins_list = require("./lib/skin_list.json");
const chars_list = require("./lib/sprite_list.json");
const images_list = require("./lib/images.json");

const Background = styled.div`
  background: #484848;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;

const CatBackground = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  overflow: hidden;
`;

const CatSearch = styled.div`
  margin-bottom: 1rem;
  background: #212121;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const SearchBox = styled.input`
  width: 100%;
`;

const CatBody = styled.div`
  height: 100%;
  padding: 1rem;
  background: #212121;
  overflow: hidden;
`;

const Nav = styled.div`
  background: #212121;
  width: 100%;
`;

const NavContainer = styled.div`
  padding: 1rem;
  display: flex;
  justify-content: space-around;
`;

const NavButton = styled.div`
  color: white;
  user-select: none;
  cursor: pointer;
  padding: 0.25rem;

  ${(props) =>
    props.primary &&
    css`
      background: white;
      color: black;
    `}
`;

const BackgroundImage = styled.img`
  width: auto;
  height: auto;
  max-width: 256px;
  max-height: 144px;
  margin: 0.25rem;
`;

const CatContent = styled.div`
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow: auto;
`;

const CharCard = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0.25rem;
  background: #484848;
  color: white;
  padding: 0.25rem;
  cursor: pointer;
`;

const CharImg = styled.img`
  width: auto;
  height: auto;
  max-height: 240px;
  max-width: 240px;
`;

const BackButton = styled.div`
  padding: 0.25rem;
  margin-top: 1rem;
  background: white;
  color: black;
  width: 50%;
  text-align: center;
  cursor: pointer;
`;

function Operators() {
  const [search, setSearch] = useState("");
  const [op, setOP] = useState("");
  const skinName = (skin) => {
    switch (skin.skinGroupId) {
      case `ILLUST_0`:
        return `Elite 0`;
      case `ILLUST_2`:
        return `Elite 2`;
      default:
        return `${skin.skinName} (${skin.skinGroupId} : ${skin.skinGroupName})`;
    }
  };
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
        {op ? <BackButton onClick={() => setOP("")}>Back</BackButton> : <></>}
      </CatSearch>
      <CatBody>
        <CatContent>
          {op
            ? skins_list[op]["skins"].map((s) => (
                <a
                  href={`./images/skins/${s.skinId}.png`}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={s.skinId}
                >
                  <CharCard>
                    <CharImg src={`./images/skins/${s.skinId}.png`}></CharImg>
                    <div>{skinName(s)}</div>
                  </CharCard>
                </a>
              ))
            : search
            ? Object.keys(skins_list)
                .filter(
                  (c) =>
                    skins_list[c]["charId"]
                      .toLowerCase()
                      .search(search.toLowerCase()) >= 0 ||
                    skins_list[c]["charName"]
                      .toLowerCase()
                      .search(search.toLowerCase()) >= 0
                )
                .map((c) => (
                  <CharCard key={c} onClick={() => setOP(c)}>
                    <CharImg
                      src={`./images/skins/${skins_list[c]["skins"][0]["skinId"]}.png`}
                    ></CharImg>
                    <div>{`${skins_list[c]["charId"]} (${skins_list[c]["charName"]})`}</div>
                  </CharCard>
                ))
            : Object.keys(skins_list).map((c) => (
                <CharCard key={c} onClick={() => setOP(c)}>
                  <CharImg
                    src={`./images/skins/${skins_list[c]["skins"][0]["skinId"]}.png`}
                  ></CharImg>
                  <div>{`${skins_list[c]["charId"]} (${skins_list[c]["charName"]})`}</div>
                </CharCard>
              ))}
        </CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Characters() {
  const [search, setSearch] = useState("");
  const [char, setChar] = useState("");
  const [images, setImages] = useState([]);

  const getImageSrc = async (c_name, n) => {
    const c = chars_list[c_name];
    if (c.FacePos.x >= 0 && c.FacePos.y >= 0) {
      var base = Object.keys(c.sprites)[Object.keys(c.sprites).length - 1];
      if (base === Object.keys(c.sprites).length) {
        return `./images/characters/${c_name}/${c["sprites"][base]}`;
      } else {
        return await mergeImages([
          {
            src: `./images/characters/${c_name}/${c["sprites"][base]["sprite"]}`,
            x: 0,
            y: 0,
          },
          {
            src: `./images/characters/${c_name}/${c["sprites"][n]["sprite"]}`,
            x: c.FacePos.x,
            y: c.FacePos.y,
          },
        ]);
      }
    } else {
      return `./images/characters/${c_name}/${c["sprites"][n]["sprite"]}`;
    }
  };

  useEffect(() => {
    var promises = "";

    if (char) {
      var sprites = chars_list[char]["sprites"];
      setImages(
        Object.keys(sprites).map((s) => (
          <a
            href={`./images/characters/${char}/${sprites[s]["sprite"]}`}
            target="_blank"
            rel="noopener noreferrer"
            key={`${char}_${s}`}
          >
            <CharCard>
              <CharImg
                src={`./images/characters/${char}/${sprites[s]["sprite"]}`}
              ></CharImg>
              <div>
                {chars_list[char]["FacePos"]["x"] > 0 &&
                chars_list[char]["FacePos"]["y"] > 0 &&
                Object.keys(sprites)[Object.keys(sprites).length - 1] === s
                  ? `base`
                  : `${s} ${sprites[s]["alias"]}`}
              </div>
            </CharCard>
          </a>
        ))
      );
    } else {
      if (search) {
        promises = Object.keys(chars_list)
          .filter((s) => s.search(search) >= 0)
          .map(async (c) => {
            const src = await getImageSrc(c, 1);
            return (
              <CharCard onClick={() => setChar(c)} key={c}>
                <CharImg src={src}></CharImg>
                <div>{c}</div>
              </CharCard>
            );
          });
      } else {
        promises = Object.keys(chars_list).map(async (c) => {
          const src = await getImageSrc(c, 1);
          return (
            <CharCard onClick={() => setChar(c)} key={c}>
              <CharImg src={src}></CharImg>
              <div>{c}</div>
            </CharCard>
          );
        });
      }
      Promise.all(promises).then((result) => {
        setImages(result);
      });
    }
  }, [search, char]);
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
        {char ? (
          <BackButton onClick={() => setChar("")}>Back</BackButton>
        ) : (
          <></>
        )}
      </CatSearch>
      <CatBody>
        <CatContent>{images.map((image) => image)}</CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Backgrounds(props) {
  const [search, setSearch] = useState("");
  const images =
    props.type === "backgrounds" ? images_list.backgrounds : images_list.images;
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
      </CatSearch>
      <CatBody>
        <CatContent>
          {search
            ? images
                .filter((i) => i.search(search) >= 0)
                .map((bg) => (
                  <a
                    href={`./images/${props.type}/${bg}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    key={bg}
                  >
                    <BackgroundImage
                      src={`./images/${props.type}/${bg}`}
                      alt={bg}
                    />
                  </a>
                ))
            : images.map((bg) => (
                <a
                  href={`./images/${props.type}/${bg}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={bg}
                >
                  <BackgroundImage
                    src={`./images/${props.type}/${bg}`}
                    alt={bg}
                  />
                </a>
              ))}
        </CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Images() {
  const [cat, setCat] = useState("");
  return (
    <Background>
      <Nav>
        <NavContainer>
          <NavButton
            primary={cat === "operators"}
            onClick={() => setCat("operators")}
          >
            Operators
          </NavButton>
          <NavButton
            primary={cat === "characters"}
            onClick={() => setCat("characters")}
          >
            Characters
          </NavButton>
          <NavButton
            primary={cat === "backgrounds"}
            onClick={() => setCat("backgrounds")}
          >
            Backgrounds
          </NavButton>
          <NavButton
            primary={cat === "images"}
            onClick={() => setCat("images")}
          >
            Images
          </NavButton>
        </NavContainer>
      </Nav>
      {cat === `backgrounds` ? <Backgrounds type="backgrounds" /> : <></>}
      {cat === `images` ? <Backgrounds type="images" /> : <></>}
      {cat === `characters` ? <Characters /> : <></>}
      {cat === `operators` ? <Operators /> : <></>}
    </Background>
  );
}

export default Images;
